				"html": "<hr>\n<p>Jeffry.in is the blog of Jeffry Wainwright, a software engineer who enjoys open source, being outside, and trying to live life with purpose.\n{: .first-paragraph}</p>\n<p>Unrelated to software, he <a href=\"//www.strava.com/athletes/722335\">rides road bikes</a>, runs and commutes as much as possible via skateboard. His latest passion is surfing! üèÑ </p>\n<p>Everything on this site was written and built by him‚Äîexcept for some potential shared videos which will be cited.</p>\n<h2>Social Networks</h2>\n<p align=\"center\">\n  <a href=\"https://github.com/yowainwright\">Github</a>&nbsp;\n  <a href=\"https://instagram.com/yowainwright\">Instagram</a>&nbsp;\n  <a href=\"https://twitter.com/yowainwright\">Twitter</a>&nbsp;\n  <a href=\"https://ratherbe.in\">Tumblr</a>&nbsp;\n  <a href=\"https://www.strava.com/athletes/722335\">Strava</a>&nbsp;\n  <a href=\"https://www.linkedin.com/in/jeffrywainwright/\">LinkedIn</a>&nbsp;\n  <a href=\"https://facebook.com/yowainwright\">Facebook</a>\n</p>\n<h2>Open Source Projects</h2>\n<p><strong>Core Contributor</strong></p>\n<ul>\n<li><a href=\"//github.com/dollarshaveclub/stickybits\">StickyBits</a>, Stickybits is a lightweight (&#x3C;2KB) alternative to <code>position: sticky</code> polyfills</li>\n<li><a href=\"//github.com/dollarshaveclub/scrolldir\">Scrolldir</a>, y-axis scroll direction without jitter</li>\n<li><a href=\"//github.com/dollarshaveclub/shave\">Shave</a>, a minimal JavaScript Plugin for Truncated text</li>\n<li><a href=\"//github.com/dollarshaveclub/reframe.js\">Reframe.js</a>, a minimal JavaScript Plugin for reframing html elements so they're an intrisic ratio of the original‚Äîgreate for embeds</li>\n</ul>\n<p><strong>Contributor</strong></p>\n<ul>\n<li><a href=\"//github.com/dollarshaveclub/postmate\">Postmate</a>, a promise-based postMessage library by <a href=\"https://github.com/jakiestfu\">Jacob Kelley</a></li>\n<li><a href=\"//github.com/MishaConway/ecto_shortcuts\">Ecto_shortcuts</a>, shortcuts for common operations in ecto by <a href=\"https://github.com/MishaConway\">Misha Conway</a></li>\n</ul>\n<h2>Some work history üíº</h2>\n<p><strong>Dollar Shave Club</strong><br/>\nEngineer<br/>\nLos Angeles, CA | March 2016 - current</p>\n<ul>\n<li>Engineer Analytics Platform</li>\n<li>Built a content platform, <a href=\"https://content.dollarshaveclub.com/\">shipped</a> September of 2016 üöÄ</li>\n<li>Maintain Open Source Products (see above)</li>\n<li>Build content applications in various languages</li>\n</ul>\n<p><strong><a href=\"http://www.billboard.com/\">Billboard</a> &#x26; <a href=\"http://www.hollywoodreporter.com/\">The Hollywood Reporter</a></strong><br/>\nFront End Engineer<br/>\nLos Angeles, CA | May 2014 - March 2016</p>\n<ul>\n<li>Design/discuss products for Billboard, THR &#x26; Pret-a-reporter re-designs (Illustrator, Photoshop, <a href=\"//basecamp.com/\">Basecamp</a>, <a href=\"//www.atlassian.com/software/jira\">Jira</a>)</li>\n<li>Build quality modular frontend templates using <a href=\"//twig.sensiolabs.org/\">Twig</a></li>\n<li>Develop exciting user experiences with Sass &#x26; JS</li>\n<li>Code robust frontend systems HTML (Twig), CSS (<a href=\"//sass-lang.com/\">Sass</a>, <a href=\"//compass-style.org/\">Compass</a>, JS (JQuery, <a href=\"//backbonejs.org/\">Backbone</a>)</li>\n<li>Tools: Jira, <a href=\"//git-scm.com/\">Git</a>, <a href=\"//gruntjs.com/\">Grunt</a>, Illustrator, Photoshop, <a href=\"//www.sublimetext.com/3\">Sublime Text</a>, <a href=\"//www.vagrantup.com/\">Vagrant</a></li>\n</ul>\n<p><strong>Team Awards üèÜ</strong></p>\n<ul>\n<li>The Hollywood Reporter, Online Magazine, <a href=\"//webbyawards.com/winners/2015/websites/general-website/magazine/the-hollywood-reporter/\">2015</a>, <a href=\"//webbyawards.com/winners/2015/websites/general-website/magazine/the-hollywood-reporter/\">2014</a></li>\n<li>The Hollywood Reporter, Magazine of the Year, <a href=\"//www.hollywoodreporter.com/news/hollywood-reporter-nominated-magazine-year-855858\">2015</a>, <a href=\"//www.hollywoodreporter.com/news/hollywood-reporter-wins-national-magazine-769564\">2014</a></li>\n</ul>\n<p><strong>KPIs üéØ</strong></p>\n<ul>\n<li>Increased page load speed</li>\n<li>Improved productivity &#x26; sustainability</li>\n<li>Increased readership (by 15% - both brands)</li>\n</ul>\n<p><strong>Outlook Amusements (<a href=\"https://www.californiapsychics.com/\">California Psychics</a>)</strong><br/>\nUser Interface Developer<br/>\nBurbank, CA | March 2012 - March 2014</p>\n<ul>\n<li>Built responsive frontend framework for New Conversion Sites</li>\n<li>Developed responsive email templating framework</li>\n<li>Maintained Model View Controller (MVC) Adaptive Site</li>\n<li>Code: HTML (<a href=\"//jade-lang.com/\">Jade</a>, <a href=\"//www.asp.net/web-forms\">.Net Webforms</a>, CSS, JS (JQuery)</li>\n<li>Tools: Photoshop, Sublime Text, <a href=\"//www.visualstudio.com/en-us/visual-studio-homepage-vs.aspx\">Visual Studio</a></li>\n</ul>\n<p><strong>KPIs üéØ</strong></p>\n<ul>\n<li>Increased user clicks (over 50% increase - email)</li>\n<li>Saved money (responsive pages vs supporting mobile &#x26; desktop websites)</li>\n</ul>\n<p><strong><a href=\"http://www.nasdaq.com/\">Nasdaq</a></strong><br/>\nUser Interface Developer<br />\nRockville, MD | Jan 2011-Jan 2012</p>\n<ul>\n<li>Built site templating system for content &#x26; data</li>\n<li>Developed &#x26; implemented an <a href=\"//styleguides.io/\">online style guide</a></li>\n<li>Designed User Interfaces Charts, Graphs &#x26; Social Area</li>\n<li>Code: .NET Web forms (HTML templating Language), CSS, JS (jQuery)</li>\n<li>Tools: <a href=\"//notepad-plus-plus.org/\">Notepad ++</a>, Visual Studio, <a href=\"//msdn.microsoft.com/en-US/library/3h0544kx(v=vs.80).aspx\">Visual Source Safe</a></li>\n</ul>\n<p><strong>KPIs üéØ</strong></p>\n<ul>\n<li>Increased production time (4x faster),</li>\n<li>Daily unique users (+300k)</li>\n</ul>\n<h2>Education üéì</h2>\n<ul>\n<li>\n<p><strong>The Illinois Institute of Art Chicago</strong></p>\n</li>\n<li>\n<p>B.F.A. Visual Communications</p>\n</li>\n<li>\n<p>Chicago, IL 2009</p>\n</li>\n<li>\n<p><strong>The University of Wisconsin - Madison</strong></p>\n</li>\n<li>\n<p>B.S. Fine Art</p>\n</li>\n<li>\n<p>Madison, WI 2003</p>\n</li>\n<li>\n<p><strong>American University</strong></p>\n</li>\n<li>\n<p>Summer Program</p>\n</li>\n<li>\n<p>Washington, DC 2000</p>\n</li>\n<li>\n<p><strong>The Art Institue of Chicago</strong></p>\n</li>\n<li>\n<p>Pre-college Program</p>\n</li>\n<li>\n<p>Chicago, IL 1999</p>\n</li>\n</ul>\n<h2>Apps</h2>\n<ul>\n<li><strong>Design</strong>: Illustrator, Indesign, Photoshop, Sketch</li>\n<li><strong>Dev</strong>: Sublime Text, Terminal, Illustrator, Indesign, Photoshop, Sketch</li>\n<li><strong>Code Management</strong>: Git, Stash, Github, BitBucket</li>\n<li><strong>CMS</strong>: Drupal, CQ5, Sitecore, Wordpress, SailThru, Strongmail, RebleMouse, MailChimp, SquareSpace, Shopify, Google Products</li>\n<li><strong>Project Management</strong> Basecamp, Bamboo, Jira, Trello</li>\n</ul>\n<h2>Code base</h2>\n<ul>\n<li><strong>HTML</strong>: Jade, Twig, Liquid, Jinja, Mustache, Handlebars</li>\n<li><strong>CSS</strong>: SASS, PostCss</li>\n<li><strong>JS</strong>: jQuery, Backbone, Underscore, Ember, Grunt, Gulp, Webpack, LoDash, Rollup, Node</li>\n<li><strong>Frameworks</strong>: Bootstrap, Semantic</li>\n</ul>",
